.intro {
    position: relative;
    padding-top: 184px;
    padding-bottom: 155px;
    @media (max-width: 1200px) {
        padding-top: 160px;
        padding-bottom: 100px;
    }
    @media (max-width: 992px) {
        padding-top: 150px;
        padding-bottom: 80px;
    }
    @media (max-width: 768px) {
        padding-top: 75px;
    }
    .swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
        margin: 0 9px;
    }

    &-content {
        position: relative;
        z-index: 1;

        &__wrapper {
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            @media (max-width: 768px) {
                flex-direction: column;
                align-items: center;
            }
        }

        &__title {
            margin-top: 104px;
            margin-right: 20px;
            @media (max-width: 768px) {
                width: 500px;
                max-width: 100%;
                margin: 15px 0 15px;
            }
        }

        &__text {
            width: 620px;
            padding: 62px 84px;
            background: $gray-opacity;
            @media (max-width: 1200px) {
                width: 500px;
                padding: 40px 60px;
            }
            @media (max-width: 992px) {
                padding: 30px;
            }
            @media (max-width: 768px) {
                max-width: 100%;
                padding: 20px;
            }
            p {
                font-size: 1.6rem;
                line-height: 1.5;
                color: $white-opacity-8;
                margin-top: 2.4rem;
                @media (max-width: 768px) {
                    margin-top: 10px;
                }
                &:first-child {
                    font-size: 2rem;
                    line-height: 1.4;
                    font-weight: 700;
                    color: $white;
                    margin-bottom: 4rem;
                    @media (max-width: 768px) {
                        margin-top: 0;
                        margin-bottom: 20px;
                    }
                }
            }
        }

        &__btn {
            width: 200px;
            margin-top: 6.4rem;
            @media (max-width: 1200px) {
                width: 180px;
            }
            @media (max-width: 992px) {
                width: 160px;
            }
            @media (max-width: 768px) {
                display: block;
                margin: 30px auto 0;
            }
        }
    }

    &-slider {
        position: absolute;
        z-index: 0;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        &__slide {
            position: relative;
            overflow: hidden;

            &::after {
                content: "";
                position: absolute;
                z-index: 2;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: $black-opacity-6;
            }

            img {
                position: absolute;
                z-index: 1;
                top: 50%;
                left: 50%;
                width: auto;
                height: 100%;
                transform: translate(-50%, -50%);
            }
        }

        &__pagination {
            display: flex;
            align-items: center;
            justify-content: center;
            position: absolute;
            z-index: 2;
            bottom: 24px;
            left: 0;
            width: 100%;

            .swiper-pagination-bullet {
                width: 8px;
                height: 8px;
                border-radius: 50%;
                background: $white-opacity-6;
                opacity: 1;

                &-active {
                    background: $white;
                }
            }
        }

        &__controls {
            display: flex;
            align-items: center;
            justify-content: space-between;
            position: absolute;
            z-index: 2;
            right: 160px;
            bottom: 24px;
            @media (max-width: 768px) {
                right: 50%;
                bottom: 35px;
                transform: translateX(50%);
            }
        }

        &__numbers {
            font-size: 1.6rem;
            line-height: 1.5;
            font-weight: 600;
            color: $white-opacity-6;
            margin: 0 34px;

            .current {
                color: $white;
            }
        }

        &__btn {
            @include containBg();
            @include transition();
            width: 24px;
            height: 24px;
            background-color: transparent;
            border: none;

            &:hover {
                transform: scale(1.1);
            }

            &:active {
                transform: scale(1);
            }

            &--prev {
                background-image: url(../images/arrow-back-white.svg);
            }

            &--next {
                background-image: url(../images/arrow-forward-white.svg);
            }
        }
    }
}