.instagram {
    padding: 96px 0;
    @media (max-width: 1200px) {
        padding: 60px 0;
    }
    @media (max-width: 992px) {
        padding: 40px 0;
    }
    @media (max-width: 768px) {
        padding: 20px 0;
    }
    &__slider {
        padding-top: 64px;
        @media (max-width: 768px) {
            padding-top: 44px;
        }
    }
    &__slide {
        height: auto;
        a {
            display: block;
            position: relative;
            width: 100%;
            height: 100%;
            overflow: hidden;
            &:hover {
                .hover {
                    transform: translateY(0);
                }
            }
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                font-family: "object-fit:cover", serif;
            }
            .hover {
                @include transition();
                display: flex;
                align-items: center;
                justify-content: center;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                padding: 15px;
                background-color: $project-overlay-base;
                transform: translateY(100%);
                .text {
                    display: flex;
                    height: 100%;
                    font-size: 13px;
                    color: $white;
                    overflow: hidden;
                }
            }
        }
    }
    &-button {
        display: none;
        align-items: center;
        justify-content: center;
        position: absolute;
        z-index: 1;
        top: 8px;
        width: 48px;
        height: 48px;
        background: $orange-dim;
        border: none;
        cursor: pointer;
        @media (max-width: 768px) {
            width: 30px;
            height: 30px;
        }
        &.show {
            display: flex;
        }
        .icon {
            display: flex;
            width: 16px;
            @media (max-width: 768px) {
                width: 10px;
            }
            svg {
                width: 100%;
            }
        }

        path {
            @include transition();
        }

        &-prev {
            right: 64px;
            @media (max-width: 768px) {
                right: 46px;
            }
            .icon {
                transform: rotate(180deg);
            }
        }

        &-next {
            right: 0;
        }

        &.swiper-button-disabled {
            background: $gray;
            cursor: auto;
            path {
                fill: $white;
            }
        }
    }
}