.production {
    padding: 96px 0;
    background: linear-gradient(to right, #ffffff, #ffffff 44%, #262626 44.01%);
    @media (max-width: 1200px) {
        padding: 60px 0;
    }
    @media (max-width: 992px) {
        padding: 40px 0;
        background: linear-gradient(to right, #ffffff, #ffffff 50%, #262626 50%);
    }
    @media (max-width: 768px) {
        padding: 20px 0 0;
        background: $white;
    }
    .swiper-container-horizontal > .swiper-pagination-bullets {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        .swiper-pagination-bullet {
            opacity: 1;
            background-color: $gray;
            margin: 0 9px;
            &-active {
                background-color: $orange-dim;
            }
        }
    }

    &__wrapper {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        justify-content: space-between;
        @media (max-width: 768px) {
            flex-direction: column;
            padding: 0;
        }
        .header-block {
            width: 100%;
            margin-bottom: 64px;
            @media (max-width: 1200px) {
                margin-bottom: 40px;
            }
            @media (max-width: 992px) {
                margin-bottom: 30px;
            }
            @media (max-width: 768px) {
                margin-bottom: 20px;
                padding: 0 20px;
            }
        }
    }

    &-slider {
        width: calc(60.6875% - 20px);
        max-width: 872px;
        margin: 0;
        margin-right: 20px;
        padding-bottom: 24px;
        @media (max-width: 1200px) {
            width: calc(65% - 20px);
        }
        @media (max-width: 768px) {
            width: calc(100% - 40px);
            margin: 0 20px;
        }
        &__btn {
            @include containBg();
            position: absolute;
            z-index: 1;
            top: calc(50% - 36px);
            width: 48px;
            height: 48px;
            border: none;
            background-image: url(../images/production-slider-arrow.svg);
            @media(max-width: 768px) {
                top: calc(50% - 32px);
                width: 32px;
                height: 32px;
            }
            &--prev {
                right: auto;
                left: 0;
                transform: rotate(180deg);
            }
            &--next {
                right: 0;
                left: auto;
            }
            &.swiper-button-disabled {
                background-image: url(../images/production-slider-arrow--disabled.svg);
            }
        }
    }

    &-slide {
        display: flex;
        width: 100%;
        picture {
            display: flex;
            img {
                width: 100%;
            }
        }
    }

    &-info {
        width: 33.6875%;
        max-width: 440px;
        margin-right: percentage(90 / 1600);
        @media(max-width: 1200px) {
            margin-right: 0;
        }
        @media (max-width: 768px) {
            width: 100%;
            max-width: none;
            margin-top: 20px;
            padding: 20px;
            background-color: #262626;
        }
        &__title {
            font-size: 2.2rem;
            line-height: 1.36;
            font-weight: 700;
            color: $white;
        }
        &__list {
            margin-top: 58px;
            list-style: none;
            @media (max-width: 1200px) {
                margin-top: 40px;
            }
            @media (max-width: 992px) {
                margin-top: 30px;
            }
            @media (max-width: 768px) {
                margin-top: 20px;
            }
        }
        &__item {
            position: relative;
            font-size: 1.6rem;
            line-height: 1.5;
            font-weight: 600;
            color: $white;
            margin-top: 32px;
            padding-left: 16px;
            @media (max-width: 1200px) {
                margin-top: 25px;
            }
            @media (max-width: 992px) {
                margin-top: 20px;
            }
            @media (max-width: 768px) {
                margin-top: 15px;
            }
            &::before {
                content: "";
                position: absolute;
                top: 9px;
                left: 0;
                width: 6px;
                height: 6px;
                border-radius: 50%;
                background: $orange-dim;
            }
            &:first-child {
                margin-top: 0;
            }
        }
        &__link {
            width: 200px;
            margin-top: 72px;
            @media (max-width: 1200px) {
                width: 180px;
                margin-top: 50px;
            }
            @media (max-width: 992px) {
                width: 160px;
                margin-top: 40px;
            }
            @media (max-width: 768px) {
                display: block;
                width: 140px;
                margin: 30px auto 0;
            }
        }
    }
}